"use client";

import { motion } from "framer-motion";

interface MarqueeProps {
    items: string[];
    speed?: number;
}

export default function Marquee({ items, speed = 20 }: MarqueeProps) {
    return (
        <div className="relative flex overflow-hidden bg-nava-green py-6 border-y border-black">
            <motion.div
                className="flex whitespace-nowrap gap-12 font-black uppercase text-2xl md:text-4xl text-black"
                animate={{ x: "-50%" }}
                transition={{
                    repeat: Infinity,
                    ease: "linear",
                    duration: speed,
                }}
            >
                {[...items, ...items, ...items, ...items].map((item, index) => (
                    <span key={index} className="flex items-center gap-12">
                        {item}
                        <span className="w-4 h-4 rounded-full bg-black block" />
                    </span>
                ))}
            </motion.div>
        </div>
    );
}
